import React, { Component } from 'react'
import './css/games.css'

export class GameInfo extends Component {
    constructor(props) {
        super(props)
        this.title = props.gameObj.gameName
        this.desc = props.gameObj.description
        this.url = props.gameObj.url
        this.state = { hideDesc: true }
    }
    render() {
        return (
            <div>
                <button> 
                    {this.title} </button>
                <p onClick={() => this.setState({hideDesc:false})}
                    className={this.state.hideDesc ? 'display' : 'hidden'}>Show More</p>
                <p className={this.state.hideDesc ? 'hidden' : 'displayGameText'}>{this.desc} </p>
                <button onClick={() => window.location.href=this.url}
                    className={this.state.hideDesc ? 'hidden' : 'displayButton'}>
                    Visit game site</button>
            </div>
        )
    }
}