import React, { Component } from 'react'
import './fonts/zig.ttf'
import './css/games.css'
import { DrawGame } from './DrawGame'
import GameData from './GameData'
import { Table, Container, Row, Col } from 'react-bootstrap'
import { GameInfo } from './GameInfo.component'
import { FiText, EnText } from './texts'

console.log(GameData)

class Games extends Component {
    windowWidth;
    constructor(props) {
      super(props)
      this.windowWidth = window.innerWidth
      this.state = { txt: props.txt, lang: 'fi', hideSidebar: false, gameInfo: '' }
    }
    static getDerivedStateFromProps(props, state) {
      if (props.txt !== state.txt) {
          if (props.txt instanceof FiText) {
            return { txt: props.txt, lang: 'fi' }
          } else if (props.txt instanceof EnText) {
            return { txt: props.txt, lang: 'en' }
          }
          
      } else {
        return null
      }
    } 
    showInfo(data) {
      if (this.state.lang === 'fi' || data.groupid === undefined) {
        this.setState({ gameInfo: 
          <div>
            <img alt=""  width="80%"
              src={`${process.env.PUBLIC_URL}/logos/` + data.groupid + `_pelilogo.png`} 
              />
            <h2>{data.gameName}</h2>
            <p>{data.description}</p>
          </div>
        })
      } else {
        this.setState({ gameInfo: 
          <div>
            <img alt=""  width="80%"
              src={`${process.env.PUBLIC_URL}/logos/` + data.groupid + `_pelilogo.png`} 
              />
            <h2>{data.gameName}</h2>
            <p>{data.description_en}</p>
          </div>
        })
      }
        
    }
    componentDidMount() {
      this.showInfo({ gameName: this.state.txt.games.defaultTitle, description: this.state.txt.games.defaultText })
      if (this.windowWidth < 768 ) {
        this.setState({hideSidebar: true})
      } else {
        this.setState({hideSidebar: false})
      }
    }
    render() {
      let games = Object.keys(GameData).map(game => 
        
        <tr width="100%" style={{color: 'white'}} key={GameData[game].groupid} >
          
          <td width="30%"> 
            <img className="gameTableImg" alt="" 
                src={`${process.env.PUBLIC_URL}/logos/` + GameData[game].groupid + `_pelilogo.png`} 
                />
          </td>
          <td width="70%" className="gameTableTitle">
              <GameInfo gameObj={GameData[game]} />
          </td>
          
        </tr>
        
      )
      return (
          
<Container fluid className="containerPadding gameMain" id="games">
<Row>
  <Col sm={7}>
  <h2 className="title2" onMouseEnter={() => this.showInfo({ gameName: this.state.txt.games.defaultTitle, 
      description: this.state.txt.games.defaultText })} >{this.state.txt.games.pageTitle}</h2>
  <section>  
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko1a} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko1b} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko1c} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko1d} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko1e} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko1f} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko2a} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko2b} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko2c} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko2d} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko2e} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko2f} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3a} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3b} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko3c} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3d} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3e} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3f} lang={this.state.lang} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
  </section>
  </Col>
  <Col sm={5} className={this.state.hideSidebar ? 'hidden' : 'gameInfo'} >
      <h2>{this.state.gameInfo}</h2>
  </Col>
</Row>
<Row>
  <Col>
    <Table striped hover variant="dark" className={this.state.hideSidebar ? '' : 'hidden'}>
      <thead>
        <tr className="tableHeader">
          <th className="tableHeaderLogo">{this.state.txt.games.tableLogo}</th>
          <th>{this.state.txt.games.tableTitle}</th>
        </tr>
      </thead>
      <tbody>{games}</tbody>
    </Table>
  </Col>
  
</Row>
</Container>
        )
    }
}

export default Games