import React, {Component} from 'react'
import { FiText, EnText } from './texts'
import Navi from './navi'
import LandingPage from './landingpage'
import Games from './games'
import Footer from './footer'

import { Route, HashRouter } from 'react-router-dom'

export class MainWrapper extends Component {
    constructor() {
        super()
        this.state = { langIsFi: true, txt: new FiText() }
        this.clickHandle = this.clickHandle.bind(this)
    }
    clickHandle() {
        if(this.state.langIsFi) {
          this.setState({langIsFi: false, txt: new EnText()})
         } else {
          this.setState({langIsFi: true, txt: new FiText()})
         } 
      }
    render() {
        return (
            <div>
                
                <HashRouter>
                    <Navi changeLang={this.clickHandle} />
                    <Route exact path="/games" render={ (props) => <Games {...props} txt={this.state.txt} /> } />   
                    <Route exact path="/" render={ (props) => <LandingPage {...props} txt={this.state.txt} /> } />
                    <Footer txt={this.state.txt} />
                </HashRouter>
                
            </div>
        )
    }
}